* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.movie {
  width: 35%;
  height: 320px;
  background-color: white;
  margin: 30px;
  padding: 20px;
  display: flex;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  position: relative;
}

img {
  width: auto;
  height: auto;
  max-width: 170px;
  max-height: 270px;
  margin-right: 35px;
  top: -40px;
  transform: translate(0, -42px);
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

.movie_data {
  margin-top: 20px;
}

.movie_title {
  margin-bottom: 10px;
  font-size: 25px;
}

.movie_year {
  margin-bottom: 10px;
}

.genres {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  color: #E3E4EB;
}

.genres_genre {
  margin-right: 8px;
}

.movie_summary {
    line-height: 25px;
    color: #BFC2D1;
}
